import { Component } from "@angular/core"
import { IonicPage, NavController, NavParams } from "ionic-angular"
import { AppState } from "../../app/app.service"
import { Http } from "@angular/http"
import "rxjs/add/operator/map"

@IonicPage({
  segment: "ship/:id"
})
@Component({
  selector: "page-ship",
  templateUrl: "ship.html"
})
export class ShipPage {
  // loading = true
  id = this.navParams.data.id
  ship
  amOwner

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public http: Http,
    public appState: AppState
  ) {}

  ionViewDidLoad() {
    console.log("shipid=", this.id)
    this.getShip(this.id)
  }

  getShip(id) {
    let url = "https://api.eth.bid/api/v1/ship?id=" + encodeURI(id) // "http://localhost:8080/api/v1/ships?address=" +
    console.log(url)
    this.http
      .get(url)
      .map(res => res.json())
      .subscribe(
        data => this.parseResult(data),
        err => console.log(err),
        () => console.log("Complete")
      )
  }

  parseResult(data) {
    console.log(data)
    // if (data.length > 0) {
    // data.forEach(ship => {
    //   ship["img"] = "randomColo" + Math.floor(Math.random() * 4.99) + ".png"
    // })
    // }
    this.ship = data
    this.amOwner = this.ship.owner == this.appState.account
    // this.loading = false
  }
}



